/** VERSION 13.01.2016 */
/**
 * MAIN STYLES
 */
*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/**
 * FRODO
 */
#frodo-wrapper {
    height: 100%
}

.frodo {
  a {
    text-decoration: none;
  }
  color: #0088e3;
  box-shadow: 0 0 10px 0 rgba(164, 164, 164, 0.9),
                  -15px 0 15px -20px rgba(164, 164, 164, 0.9), 15px 0 15px -20px rgba(164, 164, 164, 0.9);
  max-width: 400px;
  min-width: 400px;
  position: fixed;
  z-index: 10000;
  top: 50%;
  left: 50%;
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%);
  @include transition;
}

/**
 * FRODO OVERLAY
 */
.frodo-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 9999;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.70);
  @include transition;
}

/**
 * FRODO FORM
 */
.frodo-form {
  font-family: Arial, Helvetica, sans-serif;
  display:block;
  padding: 10px 0;
  background-color: #FFD716;
  background-color: #f7f7f7;
  -moz-box-shadow: 0 0 3px 3px #888;
    -webkit-box-shadow: 0 0 3px 3px #888;
  box-shadow: 0 0 3px 3px #888;
  border-radius:2px;
}


/**
 * FRODO HEDAER
 */
.frodo-header {
  font-size:20px;
  color: #FFD716;
  color: #0088E3;
  margin-top: 0;
  font-weight:bold;
  padding: 0 38px;
}
.frodo-btn-close {
    color: #000;
    float: right;
    font-size: 21px;
    font-weight: 700;
    line-height: 1;
    opacity: 0.2;
    text-shadow: 0 1px 0 #fff;
    background: rgba(0, 0, 0, 0);
    color: #000;
    border: 0 none;
    cursor: pointer;
    padding: 0;
    -webkit-transition: opacity .3s linear;
    -moz-transition: opacity .3s linear;
    -ms-transition: opacity .3s linear;
    -o-transition: opacity .3s linear;
    transition: opacity .3s linear;
}
.frodo-btn-close:focus, .frodo-btn-close:hover {
    color: #000;
    cursor: pointer;
    opacity: 0.5;
    text-decoration: none;
}
.frodo-message {
  font-size: 14px;
  text-align: center;
  font-weight: bold;

}
.frodo-message-alert, .frodo-message-success {
  padding: 10px 5px;
  margin-bottom: 20px;
  -webkit-border-radius: 2px;
     border-radius: 2px;
}
.frodo-message-alert {
    border: 1px solid #ebccd1;
    background-color: #F2DEDE;
    color: #a94442;
}
.frodo-message-success {
    background-color: #dff0d8;
    border-color: #d6e9c6;
    color: #3c763d;
}

/**
 * FRODO LOGIN FORM
 */
.frodo-login-box {
  margin: 0 auto;
  border-bottom: 1px solid #dadada;
  padding:0 20px 10px;
}
.frodo-input {
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 2px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;
    color: #555;
    display: block;
    font-size: 14px !important;
    height: 34px;
    line-height: 1.42857;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    width: 100%;
    // margin-bottom: 10px;
    //Fix  - placeholder visibility in IE and Firefox
    height: 40px;
    padding: 0 20px;
}
.frodo-input:focus {
    border-color: #66afe9;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(102, 175, 233, 0.6);
    outline: 0 none;
}
.frodo-input:focus.frodo-err {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(255, 0, 0, 0.6);
  border-color: #f00;
}
.frodo-links  {
  @include pull-left;
  a {
    color:#8a8a8a;
    display: block;
    line-height:19px;
    font-size: 12px;
    outline: none !important;
    text-decoration: underline;
  }
}
.frodo-btn-submit {
  @include pull-right;
  @include transition;
-moz-user-select: none;
  background-image: none;
  border: 1px solid #d43f3a;
  border-radius: 2px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857;
  margin-bottom: 0;
  padding: 6px 12px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  background-color: #d9534f;
  color: #fff;

    &:hover {
      background-color: #c9302c;
      border-color: #ac2925;
      color: #fff;
    }
    &:disabled {
      background-color: #DE928F;
    }
}

/**
 * FRODO SOCIAL
 */
.frodo-social-box {
  margin: 0 auto;
  padding: 28px;
  border-bottom:1px #ccc solid;
}
.frodo-social-box a {
  font-weight:bold;
  font-size:18px;
  padding:8px;
}
.frodo-social-box a i {
  font-weight:bold;
  font-size:20px;
}
.frodo-social {
  padding: 0 20px 10px;
  margin-left: -1%;
  margin-right: -1%;
  text-align: center;
  &:after {
    content: "";
    display: block;
    width: 100%;
    margin-bottom: -1.2em;
    margin-bottom: -5px;
    }
  }
.frodo-provider {
  display: inline-block;
  // width: 50%;
  width: 100%;
  min-width: 180px;
  text-align: center;
  vertical-align: top;
  padding: 2%;
  a {
    text-decoration: none;
  }
  }
.frodo-provider-mobile {
   width: 100%;
}
.frodo-provider--reset {
    border: 0 none;
    height: 0 !important;
    margin: 0 !important;
    padding: 0;
}
.frodo-btn {
  font-size: 14px;
  font-weight: normal;
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 0 5px;
  margin: 0;
  color: #fff;
  opacity: 1;
  text-decoration: none !important;
  -webkit-transition: al, .3s, ease;
  -moz-transition: al, .3s, ease;
  -ms-transition: al, .3s, ease;
  -o-transition: al, .3s, ease;
  transition: al, .3s, ease;
  line-height: 40px;
  text-align: center;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  &:hover, &:focus, &:active {
    outline: 0;
    opacity: 0.7;
    color: #fff;
    text-decoration: none !important;
  }
  i {
    font-size: 21px;
    line-height: 40px;
    padding-right: 15px;
    vertical-align: middle;
  }
}

.frodo-log-with {
    display: block;
    text-align: center;
    padding: 10px 0;
    font-weight: bold;
    text-transform: uppercase;
    color: #000;
    span {
      color: #000;
    }
}
.frodo-login-footer {
  @include clearfix;
}

/**
 * Helpers
 */
.frodo-input-wrapper { position: relative; margin: 15px 0; }

.frodo-err-input { border: 1px solid #f00 !important; }

.frodo-err-msg { font-size: 13px; color: #f00; position: absolute; right: 10px; top: -25px; line-height: 36px; }
// .frodo-err-msg + .frodo-err-msg { top: 0; }

.frodo-visible {
  opacity: 1;
  visibility: visible;
}
.frodo-hide {
  display: none !important;
}
